<script>

    import { onMount } from 'svelte';
    import Chart from 'chart.js/auto/auto.js';
	import { CSVDownloader } from 'svelte-csv';

    export let input


	let config = {
		type: 'radar',
		data: { 
			labels: Object.keys(input["Strategie und Zweck"]), // remove hardcode
			datasets: [{
				label: "Strategie und Zweck", // remove hardcode
				backgroundColor: 'rgba(255, 99, 132, 0.2)',
				borderColor: 'rgba(255, 99, 132, 0.6)',
				data: Object.values(input["Strategie und Zweck"])
				}],
			},
		options: {
			scale: {
				ticks: {
					stepSize: 1
				},
				r: {
					min: 0,
					max: 5,
				}
			},
			maintainAspectRatio: false
		},
	};


	let ctx;
	let chart;
	let dividend;
	let divisor;
	let average;
	let averages = [];


	onMount(async (promise) => {
		updateChart()
		getAverages()
	});	

	function round(value, precision) {
		var multiplier = Math.pow(10, precision || 0);
		return Math.round(value * multiplier) / multiplier;
	}

	function updateData(dimension) {
		let newConfig = {}
		let newData = []

		for (const [title, dataPoint] of Object.entries(input[dimension])) {
			newData.push(dataPoint.value)
		}

		newConfig.labels = Object.keys(input[dimension])
		newConfig.datasets = [{
			label: dimension,
			backgroundColor: 'rgba(255, 99, 132, 0.2)',
			borderColor: 'rgba(255, 99, 132, 0.6)',
			data: newData
		}]
		return newConfig
	}
	
	function updateChart() {
		for (let dimension in input) {
			ctx = document.getElementById("chart-" + dimension.replace(/\s+/g, '')).getContext("2d")
			config.data = updateData(dimension)

			chart = new Chart(ctx, config)
			chart.update()
		}
	}

	function getAverages() {
		for (let dimension in input) {
			dividend = 0
			divisor = Number(Object.keys(input[dimension]).length)
			average = 0
			
			for (let capability in input[dimension]) {
				dividend += Number(input[dimension][capability].value)
			}

			averages[dimension] = round(dividend/(divisor*1.0), 1)
		}
	}


	function prepareForCsvConversion(jsObject) {
		let newObject = []
		newObject.push({Capability: "", Bewertung: "", Begruendung: "", Firma: input.Firmeninfo.unternehmen, Bereich: input.Firmeninfo.bereich, Projekt: input.Firmeninfo.projekt, Beschreibung: input.Firmeninfo.beschreibung})
		for (const [keyDimension, valueCapability] of Object.entries(input)) {
			for (const [keyCapability, capabilityValues] of Object.entries(valueCapability)) {
				let capabilityData = {Capability: keyCapability, Bewertung: capabilityValues.value, Begruendung: capabilityValues.explanation, Firma: "", Bereich: "", Projekt: "", Beschreibung: ""}
				newObject.push(capabilityData)
			}
		}


		return newObject
	}

	function getCurrentDate() {
		const date = new Date()

		let day = date.getDate()
		let month = date.getMonth() + 1
		let year = date.getFullYear()

		return `${year}${month}${day}`
	}

</script>

<div id="auswertung">

	<CSVDownloader
	data={prepareForCsvConversion(input)}
	type={'button'}
	filename={`Export_Agile_Maturity_${getCurrentDate()}`}
	bom={true}>
	Resultate exportieren
  	</CSVDownloader>

	{#each Object.keys(input) as dimension}
	<div class="chart">
		<canvas id="chart-{dimension.replace(/\s+/g, '')}"></canvas>
		<p>Mittelwert: {averages[dimension]}</p>
	</div>
	{/each}

</div>



<style>
	.chart {
		width: 100%;
		height: 300px;
		margin: auto;
		padding-bottom: 70px;
	}
</style>